// Button
.bn {
    position: relative;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 8px;
    color: #fff;
    font: {
        size: 12px;
    }
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: .3s;
    z-index: 1;
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transform: scaleY(2);
        opacity: 0;
        transition: .3s;
        z-index: -1;
    }
    &:hover {
        &:after {
            transform: scaleY(1);
            opacity: 1;
        }
    }
}

// Navbar Button 
form {
    .bn {
        background-color: transparent;
        &::after {
            background-color: $brandColor;
        }
    }
}

// Content Button 
.price-bn {
    margin-top: 20px;
    margin-bottom: 100px;
    .bn {
        padding: 7px 25px;
        background-color: $brandColor;
        border: 1px solid transparent;
        font-weight: 600;
        &::after {
            background-color: $mainColor;
        }
        &:hover {
            border: 1px solid $mainColor
        }
    }
}