// Navigation Bar
.navbar {
    background-color: $mainColor;
    padding: 20px;
    .navbar-brand {
        color: #fff;
        font: {
            family: 'Montserrat', sans-serif;
            size: 20px;
            weight: 600
        }
        transition: .2s;
        &:hover {
            color: $brandColor;
        }
    }

    .nam {
        display: inline-block;
        color: #fff;
        padding-left: 40px;
        font-size: 14px;
        font-weight: 600
    }

    .collapse {
        .navbar-nav {
            text-align: center;
            .nav-item {
                .nav-link {
                    position: relative;
                    display: block;
                    color: #fff;
                    font: {
                        size: 14px;
                    }
                    letter-spacing: 1px;
                    text-transform: capitalize;
                    transition: .3s;
                    border-bottom: 2px solid transparent;
                    margin: 0 10px;
                    z-index: 1;
                    &:before {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        border-bottom: 2px solid #fff;
                        transform: scaleY(2);
                        opacity: 0;
                        transition: .3s;
                    }
                    &:hover {
                        &:before {
                            transform: scaleY(1);
                            opacity: 1;
                        }
                    }
                }
            }
        }
    }
}