// Content
.content {
    padding: 80px 60px;
    text-align: center;
    h2 {
        font: {
            family: 'Montserrat', sans-serif; 
            size: 40px;
            weight: 300;
        }
        margin-bottom: 60px;
        letter-spacing: 1px;
    }
    .price-offer {
        &:nth-child(2) {
            border-left: 1px solid #d0d0d0;
            border-right: 1px solid #d0d0d0;
        }
        h3 {
            font: {
                size: 30px;
                weight: 300;
            }
            margin-bottom: 20px;
            letter-spacing: 1px;
            text-transform: capitalize
        }
        .price {
            font: {
                size: 30px;
                weight: 300
            }
            margin-bottom: 20px;
            .amount {
                font-size: 60px
            }
            sup {
                top: -1.5em
            }
        }
        .price-txt {
            margin-bottom: 30px;
            p {
                margin: 0;
                font-size: 16px;
                letter-spacing: 1px;
                font-weight: 600
            }
            span {
                display: block;
                font-size: 14px;
                color: #666;
                letter-spacing: 1px;
                text-transform: capitalize
            }
        }
    }
}